
import React, { useState,useEffect } from 'react';
import { Test } from './testC';
import { Button,Text,ImageContainer } from './teststyled';
import Cactus from './.gif'
export const TestB=()=>{

    const [isOpen, setIsOpen] = useState(false);
    const [isNo,setIsNo]= useState(false);
    const [buttonsDisabled, setButtonsDisabled] = useState(false);
    const [returnHome, setReturnHome] = useState(false);
    const [isSmallScreen, setIsSmallScreen] = useState(window.innerWidth < 748);

    useEffect(() => {
        const handleResize = () => {
            setIsSmallScreen(window.innerWidth < 748);
        };

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    const handleButtonClick = () => {
        setIsOpen(true);
        setButtonsDisabled(true);
      };
      const handleButtonClickNo = () => {
        setIsNo(true);
        setButtonsDisabled(true);
      };
      const handleReturnHomeClick = () => {
        setIsOpen(false);
        setIsNo(false);
        setButtonsDisabled(false);
        setReturnHome(true);
    };
    return (
<div>
{!isOpen && !isNo && (
                <>
                    <Text>  ?</Text>
                    <Button onClick={handleButtonClick} disabled={buttonsDisabled}></Button>
                    <Button onClick={handleButtonClickNo} disabled={buttonsDisabled}></Button>
                </>
            )}
{isOpen && <Test/>}
{isNo &&(<>
<Text>  ,!</Text>
<ImageContainer 
                        marginLeft={isSmallScreen ? '120px' : '155px'} //   marginLeft  props
                    >
                        <img 
                            src={Cactus} 
                            alt='' 
                            width={isSmallScreen ? '150' : '300'} 
                            height={isSmallScreen ? '150' : '300'} 
                        />
                    </ImageContainer>
 <Button onClick={handleReturnHomeClick} return={returnHome}> </Button> 
 </>
     )}
</div>
    )
}